//  Products table
.cart.table-wrapper {
	.items {
        > .item {
            width: 100%;
            padding: 24px 0;
            font-size: 14px;

            &:first-child {
                padding-top: 0;
            }

            .item-info {
                display: flex;
                align-items: baseline;
            }

            .product-image {
                display: initial;
            }

            .product-image {
                align-self: flex-start;
            }

            .product-image-container,
            .product {
                margin-right: 24px;
            }

            .product {
                flex: 4 0 0;
                display: flex;
                flex-wrap: nowrap;
                align-items: baseline;
            }

            .price-info {
                flex: 3 0 0;
                text-align: right;
            }

            .product-info {
                flex: 1 0 0;
            }

            .product-item-name {
                height: auto;
                font-weight: normal;
                display: block;
                hyphens: manual;
                line-height: normal;
            }

            .item-options,
            .product-delivery {
                margin-top: 12px;
                margin-bottom: 0;
            }

            .item-options dt,
            .item-options dd {
                font-size: 14px;
            }

            .custom-radio {
                display: block;
                margin: 0;
                padding-left: 24px;

                + .custom-radio {
                    margin-top: 4px;
                }

                label {
                    font-size: 14px;
                    margin-left: -24px;

                    &::before {
                        top: 5px;
                    }

                    &::after {
                        top: 10px;
                    }
                }
            }

            .fieldset .field {
                margin-bottom: 0;

                label {
                    margin-bottom: 4px;
                }
                
                select {
                    width: auto;
                }
            }

            .fieldset.freq {
                padding: 12px 0 0 30px;
            }

            .field-tooltip.toggle {
                position: static;
                display: inline-block;
                vertical-align: text-top;
                height: 20px;
            }

            .field-tooltip-action {
                font-size: 20px;
            }

            .field-tooltip-action.action-help::before {
                font-family: 'mylt-icons' !important;
                content: '\e604';
            }

            .qty-controls {
                margin-left: 24px;

                .actions-toolbar {
                    min-height: 0;
                    margin-top: 8px;
                    margin-bottom: 0;
                    padding-bottom: 0;

                    .action {
                        margin-bottom: 0;

                        &.action-delete {
                            position: static;

                            &::after {
                                display: none;
                            }

                            span {
                                position: static;
                                width: auto;
                                height: auto;
                                margin: 0;
                                clip: auto;
                            }
                        }
                    }
                }
            }

            .price-subtotal {
                line-height: normal;
                margin-bottom: 4px;
            }

            .price-including-tax, .price-excluding-tax {
                font-size: 16px;
                display: inline;
                line-height: normal;
            }

            .price-discount {
                font-style: italic;
            }

            .price-login {
                margin-top: 4px;
            }
        }
    }
}
@media screen and (max-width: 1000px) {
    .cart.table-wrapper {
        .items {
            > .item {
                .product-image-container {
                    width: 64px;
                    margin-right: 16px;
                }
            }
        }
    }
}

@media screen and (max-width: 960px) {
    .cart.table-wrapper {
        .items {
            > .item {
                .item-info {
                    flex-wrap: wrap;
                }
                
                .product {
                    margin-right: 0;
                }

                .price-info {
                    margin-top: 24px;
                    flex: 1 0 100%;
                }
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .cart.table-wrapper {
        margin-top: 24px;
    }
}

@media screen and (max-width: 480px) {
    .cart.table-wrapper {
        .items {
            > .item {
                .product {
                    flex-wrap: wrap;
                }

                .product-info {
                    flex: 1 0 100%;
                }

                .qty-controls {
                    margin: 12px 0 0;
                    width: 100%;

                    .fieldset,
                    .actions-toolbar {
                        display: inline-block;
                    }

                    .fieldset {
                        margin-right: 12px;
                    }
                }
            }
        }
    }
}

