@charset "UTF-8";
@import url(https://mcstg-shop.lifetime.life/static/version1550144170/frontend/Lifetime/default/en_US/css/styles.min.css);
@import url(https://mcstg-shop.lifetime.life/static/version1550144170/frontend/Lifetime/default/en_US/css/vendor/lifetime/lifetime.min.css);
@font-face {
  font-family: "mylt-icons";
  src: url("../fonts/icons/mylt-icons.ttf?bd35lx") format("truetype"), url("../fonts/icons/mylt-icons.woff?bd35lx") format("woff"), url("../fonts/icons/mylt-icons.svg?bd35lx#mylt-icons") format("svg");
  font-weight: normal;
  font-style: normal;
}
.cart.table-wrapper .items > .item {
  width: 100%;
  padding: 24px 0;
  font-size: 14px;
}
.cart.table-wrapper .items > .item:first-child {
  padding-top: 0;
}
.cart.table-wrapper .items > .item .item-info {
  display: flex;
  align-items: baseline;
}
.cart.table-wrapper .items > .item .product-image {
  display: initial;
}
.cart.table-wrapper .items > .item .product-image {
  align-self: flex-start;
}
.cart.table-wrapper .items > .item .product-image-container,
.cart.table-wrapper .items > .item .product {
  margin-right: 24px;
}
.cart.table-wrapper .items > .item .product {
  flex: 4 0 0;
  display: flex;
  flex-wrap: nowrap;
  align-items: baseline;
}
.cart.table-wrapper .items > .item .price-info {
  flex: 3 0 0;
  text-align: right;
}
.cart.table-wrapper .items > .item .product-info {
  flex: 1 0 0;
}
.cart.table-wrapper .items > .item .product-item-name {
  height: auto;
  font-weight: normal;
  display: block;
  hyphens: manual;
  line-height: normal;
}
.cart.table-wrapper .items > .item .item-options,
.cart.table-wrapper .items > .item .product-delivery {
  margin-top: 12px;
  margin-bottom: 0;
}
.cart.table-wrapper .items > .item .item-options dt,
.cart.table-wrapper .items > .item .item-options dd {
  font-size: 14px;
}
.cart.table-wrapper .items > .item .custom-radio {
  display: block;
  margin: 0;
  padding-left: 24px;
}
.cart.table-wrapper .items > .item .custom-radio + .custom-radio {
  margin-top: 4px;
}
.cart.table-wrapper .items > .item .custom-radio label {
  font-size: 14px;
  margin-left: -24px;
}
.cart.table-wrapper .items > .item .custom-radio label::before {
  top: 5px;
}
.cart.table-wrapper .items > .item .custom-radio label::after {
  top: 10px;
}
.cart.table-wrapper .items > .item .fieldset .field {
  margin-bottom: 0;
}
.cart.table-wrapper .items > .item .fieldset .field label {
  margin-bottom: 4px;
}
.cart.table-wrapper .items > .item .fieldset .field select {
  width: auto;
}
.cart.table-wrapper .items > .item .fieldset.freq {
  padding: 12px 0 0 30px;
}
.cart.table-wrapper .items > .item .field-tooltip.toggle {
  position: static;
  display: inline-block;
  vertical-align: text-top;
  height: 20px;
}
.cart.table-wrapper .items > .item .field-tooltip-action {
  font-size: 20px;
}
.cart.table-wrapper .items > .item .field-tooltip-action.action-help::before {
  font-family: "mylt-icons" !important;
  content: "";
}
.cart.table-wrapper .items > .item .qty-controls {
  margin-left: 24px;
}
.cart.table-wrapper .items > .item .qty-controls .actions-toolbar {
  min-height: 0;
  margin-top: 8px;
  margin-bottom: 0;
  padding-bottom: 0;
}
.cart.table-wrapper .items > .item .qty-controls .actions-toolbar .action {
  margin-bottom: 0;
}
.cart.table-wrapper .items > .item .qty-controls .actions-toolbar .action.action-delete {
  position: static;
}
.cart.table-wrapper .items > .item .qty-controls .actions-toolbar .action.action-delete::after {
  display: none;
}
.cart.table-wrapper .items > .item .qty-controls .actions-toolbar .action.action-delete span {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  clip: auto;
}
.cart.table-wrapper .items > .item .price-subtotal {
  line-height: normal;
  margin-bottom: 4px;
}
.cart.table-wrapper .items > .item .price-including-tax, .cart.table-wrapper .items > .item .price-excluding-tax {
  font-size: 16px;
  display: inline;
  line-height: normal;
}
.cart.table-wrapper .items > .item .price-discount {
  font-style: italic;
}
.cart.table-wrapper .items > .item .price-login {
  margin-top: 4px;
}

@media screen and (max-width: 1000px) {
  .cart.table-wrapper .items > .item .product-image-container {
    width: 64px;
    margin-right: 16px;
  }
}
@media screen and (max-width: 960px) {
  .cart.table-wrapper .items > .item .item-info {
    flex-wrap: wrap;
  }
  .cart.table-wrapper .items > .item .product {
    margin-right: 0;
  }
  .cart.table-wrapper .items > .item .price-info {
    margin-top: 24px;
    flex: 1 0 100%;
  }
}
@media screen and (max-width: 768px) {
  .cart.table-wrapper {
    margin-top: 24px;
  }
}
@media screen and (max-width: 480px) {
  .cart.table-wrapper .items > .item .product {
    flex-wrap: wrap;
  }
  .cart.table-wrapper .items > .item .product-info {
    flex: 1 0 100%;
  }
  .cart.table-wrapper .items > .item .qty-controls {
    margin: 12px 0 0;
    width: 100%;
  }
  .cart.table-wrapper .items > .item .qty-controls .fieldset,
.cart.table-wrapper .items > .item .qty-controls .actions-toolbar {
    display: inline-block;
  }
  .cart.table-wrapper .items > .item .qty-controls .fieldset {
    margin-right: 12px;
  }
}